header {
  background-color: rgb(47, 212, 234);
  width: 100%;
  overflow-x: hidden;
}
.custom-btn {
  color: #fff;
}
.custom-btn:hover {
  background-color: #fff;
  color: black;
}
.shopnow {
  padding-bottom: 1rem;
}

.content1 {
  animation: 0.5s ease-out 0s 1 slideInFromLeft;
  position: relative;
}

.content2 {
  animation: 1s ease-out 0s 1 slideInFromRight;
  position: relative;
  z-index: 3;
}

.content1 h1 {
  font-size: 4rem;
  font-weight: bolder;
}
.content1 p {
  font-size: 1.1rem;
}
.white-circle {
  width: 180px;
  height: 360px;
  background-color: white;
  border-bottom-left-radius: 180px;
  border-top-left-radius: 180px;
  right: 0;
  border-left: 0;
  position: absolute;
}
.orange-circle {
  width: 15rem;
  height: 15rem;
  background-color: rgb(255, 93, 0);
  position: absolute;
  right: 8rem;
  z-index: 0;
}
@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideInFromRight {
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
}
@media only screen and (max-width: 992px) {
  .shopnow {
    display: none;
  }
}
.rating {
  font-size: 20px;
}

.clicked {
  color: yellow;
}
.bg {
  color: #dddddd;
}
.bannerfeatures {
  width: 100%;
  background-size: cover;
  background-position: center;
  border-radius: 10px 10px 10px 10px;
}
